import http from '@/components/utils/http.js';
import listCell from '@/components/mix-list-cell';
import { mapMutations } from 'vuex';
let startY = 0,
	moveY = 0,
	pageAtTop = true;
export default {
	components: {
		listCell
	},
	data() {
		return {
			title: '个人中心',
			coverTransform: 'translateY(0px)',
			coverTransition: '0s',
			moving: false,
			userinfo: '',
			shareInvitation:false,
			invitation:"",
			sharetitle:"良吉康-最火的社交新零售商城！",
			shareimg:'/img/banner2.jpg',
			shareList :[{
							type: 1,
							icon: '/static/temp/share_wechat.png',
							text: '微信好友'
						},
						{
							type: 2,
							icon: '/static/temp/share_moment.png',
							text: '朋友圈'
						},
						{
							type: 3,
							icon: '/static/temp/share_qq.png',
							text: 'QQ好友'
						}]
		};
	},

	methods: {
		...mapMutations(['logout']),
		/**
		 * 统一跳转接口,拦截未登录路由
		 * navigator标签现在默认没有转场动画，所以用view
		 */
		navTo(url) {
			uni.navigateTo({
				url
			});
		},

		/**
		 *  会员卡下拉和回弹
		 *  1.关闭bounce避免ios端下拉冲突
		 *  2.由于touchmove事件的缺陷（以前做小程序就遇到，比如20跳到40，h5反而好很多），下拉的时候会有掉帧的感觉
		 *    transition设置0.1秒延迟，让css来过渡这段空窗期
		 *  3.回弹效果可修改曲线值来调整效果，推荐一个好用的bezier生成工具 http://cubic-bezier.com/
		 */
		coverTouchstart(e) {
			if (pageAtTop === false) {
				return;
			}
			this.coverTransition = 'transform .1s linear';
			startY = e.touches[0].clientY;
		},
		coverTouchmove(e) {
			moveY = e.touches[0].clientY;
			let moveDistance = moveY - startY;
			if (moveDistance < 0) {
				this.moving = false;
				return;
			}
			this.moving = true;
			if (moveDistance >= 80 && moveDistance < 100) {
				moveDistance = 80;
			}

			if (moveDistance > 0 && moveDistance <= 80) {
				this.coverTransform = `translateY(${moveDistance}px)`;
			}
		},
		coverTouchend() {
			if (this.moving === false) {
				return;
			}
			this.moving = false;
			this.coverTransition = 'transform 0.3s cubic-bezier(.21,1.93,.53,.64)';
			this.coverTransform = 'translateY(0px)';
		},
		
		getinvitation(){
			let opts = {
				url: '/base/getinvitation/',
				method: 'post'
			};
			let param = {};
			http.httpTokenRequest(opts, param).then(
				res => {
					if (res.data['code'] == 0) {
						this.shareInvitation=true;
						this.invitation=res.data['invitation'];
						this.shareimg=res.data['shareimg'];
						this.sharetitle=res.data['sharetitle'];
						
					}else{
						this.$api.msg(res.data.msg);
					}
				},
				error => {
					console.log(error);
				}
			);
			
			
		},
		hideModal(e) {
			this.shareInvitation = false;
		},
		
		shareToFriend(type){
			
			
			
			
		},
		toLogout() {
			uni.showModal({
				content: '确定要退出登录么',
				success: e => {
					if (e.confirm) {
						this.logout();
						setTimeout(() => {
							uni.redirectTo({
								url:"./logon"
							})
						}, 200);
					}
				}
			});
		}
	},
	
	onShareAppMessage: function( options ){
	　　var that = this;
	　　// 设置菜单中的转发按钮触发转发事件时的转发内容
	　　var shareObj = {
	　　　　title: this.sharetitle,        // 默认是小程序的名称(可以写slogan等)
	　　　　path: '/pages/person/share?v='+this.invitation,        // 默认是当前页面，必须是以‘/’开头的完整路径
	　　　　imageUrl: this.apiServer+this.shareimg,     //自定义图片路径，可以是本地文件路径、代码包文件路径或者网络图片路径，支持PNG及JPG，不传入 imageUrl 则使用默认截图。显示图片长宽比是 5:4
	　　　　success: function(res){
	　　　　　　// 转发成功之后的回调
	　　　　　　if(res.errMsg == 'shareAppMessage:ok'){
		
	　　　　　　}
	　　　　},
	　　　　fail: function(){
	　　　　　　// 转发失败之后的回调
	　　　　　　if(res.errMsg == 'shareAppMessage:fail cancel'){
	　　　　　　　　// 用户取消转发
	　　　　　　}else if(res.errMsg == 'shareAppMessage:fail'){
	　　　　　　　　// 转发失败，其中 detail message 为详细失败信息
	　　　　　　}
	　　　　},
	　　　　complete: function(){
	　　　　　　// 转发结束之后的回调（转发成不成功都会执行）
	　　　　}
	　　};
	　　// 来自页面内的按钮的转发
	　　
	　　return shareObj;
	},
	
	onLoad() {
		
		if (global.islogon() == false) {
			uni.redirectTo({
				url:"./logon"
			})
		} else {
			// let token = uni.getStorageSync('token');
			let opts = {
				url: '/base/getuserinfo/',
				method: 'post'
			};
			let param = {};
			http.httpTokenRequest(opts, param).then(
				res => {
					if (res.data['code'] == 0) {
						this.userinfo = res.data['userinfo'];
					}
				},
				error => {
					console.log(error);
				}
			);
		}
	}
};
